@import "./variable.css";

body{
    background-color: white;
}

.navigate{
    background-color: #ecf2ff;
    display: flex;
    width: 100%;
    height: 15%;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
}


.title{
    font-size: 48px;
    font-weight: 600;
    color: black;
}

.home__anchor{
    text-decoration: none !important;
    color: black;
    position: absolute;
    left: 5vh;
    background-color: #ecf2ff;
}
